/* @value colors: '../../colors.css'
@value blue from colors; */

:import('../../colors.css') {
  blue: blue;
  orange: orange;
  gray: backgroundGray;
  backgroundGray: backgroundGray;
  darkShadow: darkShadow;
  lightShadow: lightShadow;
  maxWidth: maxWidth;
}

.Banner {
  position: relative;
  width: 100%;
  height: 350px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Banner::before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 3px;
  left: 0;
  -webkit-filter:blur(5px) brightness(150%) grayscale(20%);
          filter:blur(5px) brightness(150%) grayscale(20%);
  background: url('./Picture-5.jpg');
  background-position: 0% 20%;
  background-size: cover;

}

.GetStarted {
  position: relative;
  width: 400px;
  height: 247px;
  left: calc(50% - 200px);
  top: calc(50% - 123.5px);
  padding: 15px;
  border-radius: 4px;
  text-align: center;
  -webkit-box-shadow: darkShadow;
          box-shadow: darkShadow;
  background-color: backgroundGray;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
  color: blue;
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}

.Content {
  background-color: gray;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Features {
  max-width: maxWidth;
  width: 100%;
  height: 500px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.Feature {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 20px;
  height: 250px;
  width: 220px;
  background: white;
  border-radius: 4px;
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
  text-align: center;
}

.FTitle {
  width: 100%;
}

.FIcon {
  font-size: 50px;
}

.Examples {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  max-width: maxWidth;
  width: 100%;
  margin-bottom: 20px;
}



