:import('../../../colors.css') {
  blue: blue;
  orange: orange;
  lightestShadow: lightestShadow;
  lightShadow: lightShadow;
}

.Button {
  background-color: white;
  border: none;
  -ms-flex-item-align: center;
      align-self: center;
  margin: 13px;
  padding: 7px;
  border-radius: 3px;
  border: 1px solid #d3d2d4;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  font-size: inherit;
  letter-spacing: .1em;
  cursor: pointer;
  -webkit-box-shadow: lightestShadow;
          box-shadow: lightestShadow;
}

.Primary {
  color: blue;
}

.Secondary {
  color: white;
  background-color: orange;
}

.Active {
  color: white;
  background-color: blue;
}

.Primary:hover {
  color: white;
  background-color: blue;
}

.Secondary:hover {
  color: white;
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
}



