:import('../../colors.css'){
  gray: backgroundGray;
  blue: blue;
  lightShadow: lightShadow;
}

.Container {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.SignupContainer {
  background: gray;
  margin-top: 110px;
  border-radius: 4px;
  padding: 15px;
  width: 66%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-flow: column;
      flex-flow: column;
}

.Title {
  color: blue;
  letter-spacing: .09em;
}

.Form > div {
  width: 90%;
}

.Radios {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 25%;
  margin: 10px;
}

.ErrorMsg {
  color: red;
  text-align: center;
  height: 20px;
}



