.Container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  background-color: white;
  border: 1px solid #ddd;
}

.Avatar {
  margin: 20px;
  width: 100px;
}

.Row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  width: 150px;
}

.Role {
  margin: 20px;
}

.Icon {
  padding: 5px;
  cursor: pointer;
  font-size: 1em;
  color: black;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.Icon:hover {
  color: #888;
}

@media (max-width: 920px) {
  .Container {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
  .Row {
    width: 100px;
  }
}


