*, *:before, *:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
}

body {
  max-width: 100vw;
  /* overflow-x: hidden; */
  background-size: 30px 30px;
  background-image: -webkit-linear-gradient(left, #f1f1f1 1px, transparent 1px), -webkit-linear-gradient(top, #f1f1f1 1px, transparent 1px) !important;
  background-image: -o-linear-gradient(left, #f1f1f1 1px, transparent 1px), -o-linear-gradient(top, #f1f1f1 1px, transparent 1px) !important;
  background-image: linear-gradient(to right, #f1f1f1 1px, transparent 1px), linear-gradient(to bottom, #f1f1f1 1px, transparent 1px) !important;
  font-family: 'Voces', 'sans-serif';
}
.FlexCol {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  width: 100%;
}

.FlexRow {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.Link {
  text-decoration: none;
}

.Link:hover {
  text-decoration: none;
}

a {
  text-decoration: none;
  color: inherit;
}


