:import('../../colors.css'){
  gray: backgroundGray;
  blue: blue;
  lightShadow: lightShadow;
}

.Container{
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.LoginContainer {
  background: gray;
  margin-top: 110px;
  border-radius: 4px;
  padding: 15px;
  width: 66%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Title {
  color: blue;
  letter-spacing: .09em;
}

.ErrorMsg {
  color: red;
  height: 20px;
  text-align: center;
}

.Form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 90%;
}

.Loading {
  position: fixed;
  margin: auto;
  z-index: 1000;
}



