@value colors: '../../../colors.css';
@value blue from colors;

.Container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  min-height: 500px;
}

.Radios {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
}

.SubContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  width: 100%;
}

.StudentList {
  margin: 10px 0 10px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: start;
      align-items: flex-start;
  max-height: 300px;
  overflow-y: scroll;
  width: 100%;
}

.Student {
  color: black;
  padding: 7px;
  width: 100%;
  text-align: left;
  cursor: pointer;
}

.Student:hover {
  color: blue;
}

.Selected {
  color: #999;
}

.EvenStudent {
  background-color: #ddd;
}



