@value colors: '../../colors.css';
@value blue from colors;

.Container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 100%;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 2px;
  background-color: #f9f9f9;
  -webkit-box-shadow: 0px 3px 10px -2px rgba(0,0,0,0.28);
          box-shadow: 0px 3px 10px -2px rgba(0,0,0,0.28);
}

.ChatScroll {
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  padding: 10px;
}

.ChatScroll::-webkit-scrollbar {
  width: 0.5em;
  height: 0.09em;
  background-color: #eee;
}

.ChatScroll::-webkit-scrollbar-thumb {
  background-color: blue;
  border-radius: 3px;
}

.ChatInput {
  -ms-flex-item-align: center;
      align-self: center;
  width: 90%
}

.Entry {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  /* width: 90%; */
  /* border: 1px solid red; */
}

.Timestamp {
  font-style: italic;
  color: #999;
  font-size: .8em;
  -ms-flex-item-align: end;
      align-self: flex-end;
}



