:import('../../../colors.css') {
  blue: blue;
  darkShadow: darkShadow;
}

.Nav {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 60px;
  background-color: blue;
  -ms-flex-pack: center;
      justify-content: center;
  /* border-radius: 3px; */
  -webkit-box-shadow: darkShadow;
          box-shadow: darkShadow;
  z-index: 1000;
}

.NavContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 100%;
  width: 1300px;
}

.NavList{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  width: 50%;
}

.Logo {
  font-size: 30px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}



