@value colors: '../../../colors.css';
@value blue from colors;

.Container {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  background-color: #f9f8f7;
  padding: 30px;
  border: 1px solid #d8d9da;
  border-radius: 3px;
  -webkit-box-shadow: 0px 3px 10px -2px rgba(0,0,0,0.28);
          box-shadow: 0px 3px 10px -2px rgba(0,0,0,0.28);
  -webkit-transition: all .2s ease-out;
  -o-transition: all .2s ease-out;
  transition: all .2s ease-out;
  width: 100%;
}

.Title {
  font-size: 1.6em;
  letter-spacing: .06em;
  margin-bottom: 10px;
  border-bottom: 1px solid #ddd;
  color: blue;
}

.Content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  width: 90%;
  /* color: orange; */
}

.Center {-ms-flex-align: center;align-items: center;}
.Left {-ms-flex-align: start;align-items: flex-start;}
.Right {-ms-flex-align: end;align-items: flex-end;}

.Icons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 50px;
  position: absolute;
  left: 10px;
  top: 10px;
  color: #666;
}

.Notification {
  position: absolute;
  top: -10px;
  right: 5px;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background-color: red;
  color: white;
  text-align: center;
  -webkit-text-stroke-width: .01em;
  -webkit-text-stroke-color: #333;
}



